import React from "react";
import { Day } from "../types";
import OverdueBadge from "./OverdueBadge";
import { dayPalette, DayPalette } from "../lib/palette";
import { cls } from "./_utils";

interface Props {
  day: Day;
  index: number;
  currentDayIndex: number;
  onToggleTask: (taskId: string, nextCompleted: boolean) => void;
  icon: React.ReactNode;
  palette?: DayPalette;
  dominant?: boolean;
}

const GridOverlay = () => (
  <div
    aria-hidden="true"
    className="absolute inset-0 opacity-[0.08] pointer-events-none rounded-glass"
    style={{
      backgroundImage:
        "linear-gradient(transparent, transparent 95%, rgba(255,255,255,0.25) 96%, transparent 97%), " +
        "linear-gradient(90deg, transparent, transparent 95%, rgba(255,255,255,0.25) 96%, transparent 97%)",
      backgroundSize: "100% 32px, 32px 100%"
    }}
  />
);

const DayCard: React.FC<Props> = ({
  day,
  index,
  currentDayIndex,
  onToggleTask,
  icon,
  palette = dayPalette(index),
  dominant = false
}) => {
  const isPast = index < currentDayIndex;
  const isCurrent = index === currentDayIndex;
  const editable = !isPast;

  const statusColor =
    day.status === "completed" ? "text-emerald-300" : day.status === "missed" ? "text-rose-300" : "text-white/70";
  const statusLabel =
    day.status === "completed" ? "Completed" : day.status === "missed" ? "Missed" : "Pending";

  return (
    <article
      className={cls(
        "relative rounded-glass border border-white/10 backdrop-blur p-6 shadow-glass overflow-hidden",
        "transition-all duration-300",
        dominant ? "ring-1" : ""
      )}
      role="region"
      aria-label={`Day ${day.dayNumber}`}
      style={{
        background: `linear-gradient(180deg, ${palette.from}0f, ${palette.to}14), rgba(255,255,255,0.03)`,
        boxShadow: dominant
          ? "0 0 40px rgba(0,0,0,0.5), 0 0 64px rgba(110,249,182,0.15)"
          : "0 8px 24px rgba(0,0,0,0.45)",
        // @ts-expect-error custom CSS var
        "--tw-ring-color": palette.ring
      } as React.CSSProperties}
    >
      <GridOverlay />

      <div className="mb-4">
        <div
          className="relative w-14 h-14 rounded-2xl"
          style={{
            background: `linear-gradient(180deg, rgba(255,255,255,0.06), rgba(255,255,255,0.02))`,
            boxShadow: `0 0 24px ${palette.ring}55, inset 0 0 0 1px rgba(255,255,255,0.08)`
          }}
        >
          <div
            className="absolute -inset-[1px] rounded-2xl"
            style={{
              background: `linear-gradient(135deg, ${palette.from}, ${palette.to})`,
              filter: "blur(10px)",
              opacity: 0.25
            }}
            aria-hidden="true"
          />
          <div className="relative grid place-items-center w-full h-full">{icon}</div>
        </div>
      </div>

      <div className="flex items-center justify-between mb-3">
        <div className="flex items-center gap-3">
          <h2 className="text-xl font-semibold text-white/95">Day {day.dayNumber}</h2>
          <div className={cls("text-sm", statusColor)}>{statusLabel}</div>
        </div>
        <div className="text-xs text-white/70">
          XP {day.xpReward ?? 100} • {day.estMinutes ?? 30}m
        </div>
      </div>

      {day.status === "missed" && (
        <div className="mb-3 text-sm text-amber-300/90">Missed — moved to next day</div>
      )}

      <ul className="space-y-2">
        {day.tasks.length === 0 && <li className="text-sm text-white/60">No tasks yet.</li>}
        {day.tasks.map((t) => {
          const disabled = !editable;
          return (
            <li key={t.id} className="flex items-start gap-3">
              <input
                type="checkbox"
                className="mt-0.5 h-4 w-4 rounded border-white/20 bg-white/5"
                checked={t.completed}
                disabled={disabled}
                onChange={(e) => onToggleTask(t.id, e.currentTarget.checked)}
                aria-disabled={disabled}
                title={disabled ? "This day has passed — tasks were carried forward." : "Toggle task"}
              />
              <div className="flex-1">
                <div className={cls("text-sm", t.completed ? "line-through text-white/50" : "text-white/90")}>
                  {t.title}
                </div>
                <div className="flex flex-wrap items-center gap-2 mt-1">
                  {t.carryover && <OverdueBadge fromDay={t.fromDay} />}
                </div>
              </div>
            </li>
          );
        })}
      </ul>

      <div className="mt-4 text-xs text-white/55">
        {editable
          ? isCurrent
            ? "You're on today's card — complete all 5 to pass."
            : "Future day — you may pre-complete tasks."
          : "Past day — read-only. Incomplete tasks were carried forward."}
      </div>
    </article>
  );
};

export default DayCard;